"use client";

import { 
  Box, 
  Flex, 
  Heading, 
  Input, 
  IconButton, 
  HStack, 
  Text,
  Menu, // Added for Notifications
} from "@chakra-ui/react";
import { 
  LuSearch, 
  LuBell, 
  LuMoon, 
  LuSun, 
  LuCircleUser,
  LuDot
} from "react-icons/lu";
import { useColorMode } from "../ui/color-mode"; // Use standard v3 hook
import { useSession } from "next-auth/react";
import { useState } from "react";

export default function Topbar() {
  const { colorMode, toggleColorMode } = useColorMode();
  const { data: session } = useSession();
  const [searchQuery, setSearchQuery] = useState("");

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Searching for:", searchQuery);
    // You can redirect to: router.push(`/search?q=${searchQuery}`)
  };

  return (
    <Flex
      h="70px"
      align="center"
      justify="space-between"
      px={6}
      bg="white/80"
      _dark={{ bg: "gray.900/80" }}
      backdropFilter="blur(10px)"
      position="sticky"
      top="0"
      zIndex="10"
      borderBottom="1px solid"
      borderColor="gray.100"
      _darkBorderColor="gray.800"
    >
      {/* Left Side */}
      <HStack gap={4}>
        <Heading size="md" fontWeight="semibold">
          Overview
        </Heading>
      </HStack>

      {/* Functional Search Bar */}
      <Box display={{ base: "none", md: "block" }} maxW="400px" w="full" mx={8}>
        <form onSubmit={handleSearch}>
          <Box position="relative">
            <Box
              position="absolute"
              left="3"
              top="50%"
              transform="translateY(-50%)"
              zIndex="1"
              color="gray.400"
            >
              <LuSearch size={18} />
            </Box>
            <Input
              placeholder="Search reports or metrics..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              pl="10"
              bg="gray.100"
              border="none"
              _focus={{ bg: "white", ring: "2px", ringColor: "teal.500" }}
              _dark={{ bg: "gray.800", _focus: { bg: "gray.700" } }}
              borderRadius="full"
            />
          </Box>
        </form>
      </Box>

      {/* Right Side: Actions & Profile */}
      <HStack gap={3}>
        
        {/* Functional Notifications Menu */}
        <Menu.Root>
          <Menu.Trigger asChild>
            <IconButton
              variant="ghost"
              aria-label="Notifications"
              borderRadius="full"
              color="gray.600"
              _dark={{ color: "gray.400" }}
            >
              <LuBell size={20} />
            </IconButton>
          </Menu.Trigger>
          <Menu.Content>
            <Menu.Item value="notif-1" gap={2}>
              <LuDot color="teal" size={24} /> 
              <Box>
                <Text fontSize="sm" fontWeight="bold">New Report Ready</Text>
                <Text fontSize="xs" color="gray.500">Your monthly summary is here.</Text>
              </Box>
            </Menu.Item>
            <Menu.Item value="notif-2" gap={2}>
              <LuDot color="gray" size={24} />
              <Box>
                <Text fontSize="sm">System Update</Text>
                <Text fontSize="xs" color="gray.500">Scheduled maintenance at 2AM.</Text>
              </Box>
            </Menu.Item>
          </Menu.Content>
        </Menu.Root>

        {/* Real Theme Toggle */}
        <IconButton
          variant="ghost"
          aria-label="Toggle Theme"
          borderRadius="full"
          onClick={toggleColorMode}
        >
          {colorMode === "light" ? <LuMoon size={20} /> : <LuSun size={20} />}
        </IconButton>

        {/* Dynamic User Profile */}
        <HStack 
          gap={3} 
          pl={2} 
          cursor="pointer" 
          _hover={{ opacity: 0.8 }}
        >
          <Box textAlign="right" display={{ base: "none", sm: "block" }}>
            <Text fontSize="sm" fontWeight="bold" lineHeight="1">
              {session?.user?.name || "User"}
            </Text>
            <Text fontSize="xs" color="gray.500">
              Admin
            </Text>
          </Box>
          <Box
            w="40px"
            h="40px"
            bg="teal.500"
            borderRadius="full"
            display="flex"
            alignItems="center"
            justifyContent="center"
            color="white"
            overflow="hidden"
          >
            {session?.user?.image ? (
               <img src={session.user.image} alt="Profile" />
            ) : (
               <LuCircleUser size={24} />
            )}
          </Box>
        </HStack>
      </HStack>
    </Flex>
  );
}